<template>
  <div id="app">
    <!-- Ìó§Îçî -->
    <header>
      <div class="header-top">
        <div class="logo">
          <h1>YeonPlay</h1>
        </div>
        <nav class="menu">
          <router-link to="/">Ìôà</router-link>
          <router-link to="/popular">ÎåÄÏÑ∏ ÏΩòÌÖêÏ∏†</router-link>
          <router-link to="/now_playing">ÌòÑÏû¨ ÏÉÅÏòÅ Ï§ë</router-link>
          <router-link to="/genres">Ïû•Î•¥Î≥Ñ ÏΩòÌÖêÏ∏†</router-link>
          <router-link to="/wishlist">ÎÇ¥Í∞Ä Ï∞úÌïú ÏΩòÌÖêÏ∏†</router-link>
        </nav>
        <div class="auth">
          <span v-if="isLoggedIn" class="username">{{ loggedInUser }}Îãò</span>
          <button v-if="isLoggedIn" @click="logout" class="logout-btn">Î°úÍ∑∏ÏïÑÏõÉ</button>
          <button v-else @click="loginWithKakao" class="login-btn">Î°úÍ∑∏Ïù∏</button>
        </div>
      </div>
    </header>
    <main>
      <router-view />
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isLoggedIn: false,
      loggedInUser: "",
    };
  },
  methods: {
    /** Ïπ¥Ïπ¥Ïò§ Î°úÍ∑∏Ïù∏ */
    loginWithKakao() {
      const kakaoApiKey = process.env.VUE_APP_KAKAO_API_KEY;
      const redirectUri = process.env.VUE_APP_KAKAO_REDIRECT_URI;

      if (!window.Kakao || !window.Kakao.isInitialized()) {
        window.Kakao.init(kakaoApiKey); // Ïπ¥Ïπ¥Ïò§ SDK Ï¥àÍ∏∞Ìôî
      }

      // Ïπ¥Ïπ¥Ïò§ Î°úÍ∑∏Ïù∏ Ï∞Ω Ìò∏Ï∂ú
      window.Kakao.Auth.authorize({
        redirectUri, // prompt Ï†úÍ±∞
      });
    },

    /** Î°úÍ∑∏ÏïÑÏõÉ */
    async logout() {
      try {
        // 1. Ïπ¥Ïπ¥Ïò§ ÏÑúÎ≤Ñ Î°úÍ∑∏ÏïÑÏõÉ
        if (window.Kakao && window.Kakao.Auth) {
          await window.Kakao.Auth.logout();
          console.log("Ïπ¥Ïπ¥Ïò§ ÏÑ∏ÏÖò Î°úÍ∑∏ÏïÑÏõÉ ÏôÑÎ£å");
        }

        // 2. Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄ, ÏÑ∏ÏÖò Ïä§ÌÜ†Î¶¨ÏßÄ Îç∞Ïù¥ÌÑ∞ ÏÇ≠Ï†ú
        localStorage.clear(); // Î™®Îì† localStorage Îç∞Ïù¥ÌÑ∞ ÏÇ≠Ï†ú
        sessionStorage.clear(); // Î™®Îì† sessionStorage Îç∞Ïù¥ÌÑ∞ ÏÇ≠Ï†ú

        // 3. Vue ÏÉÅÌÉú Ï¥àÍ∏∞Ìôî
        this.isLoggedIn = false;
        this.loggedInUser = "";

        // 4. Ìôà ÌôîÎ©¥ÏúºÎ°ú Ïù¥Îèô
        alert("Î°úÍ∑∏ÏïÑÏõÉÎêòÏóàÏäµÎãàÎã§. Î™®Îì† Ï†ïÎ≥¥Í∞Ä Ï¥àÍ∏∞ÌôîÎê©ÎãàÎã§.");
        this.$router.push("/");
      } catch (error) {
        console.error("Î°úÍ∑∏ÏïÑÏõÉ Ï§ë Ïò§Î•ò Î∞úÏÉù:", error);
        alert("Î°úÍ∑∏ÏïÑÏõÉ Ï§ë Î¨∏Ï†úÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.");
      }
    },

    /** Î°úÍ∑∏Ïù∏ ÏÉÅÌÉú ÌôïÏù∏ */
    async checkLoginStatus() {
      const accessToken = localStorage.getItem("accessToken");

      if (accessToken) {
        try {
          const response = await fetch("https://kapi.kakao.com/v2/user/me", {
            headers: { Authorization: `Bearer ${accessToken}` },
          });
          const data = await response.json();

          if (data.kakao_account) {
            this.loggedInUser = data.kakao_account.profile.nickname;
            this.isLoggedIn = true;
            localStorage.setItem("loggedInUser", this.loggedInUser);
            localStorage.setItem("isLoggedIn", "true");
          }
        } catch (error) {
          console.error("ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥Î•º Í∞ÄÏ†∏Ïò§Îäî Ï§ë Ïò§Î•ò Î∞úÏÉù:", error);
          this.isLoggedIn = false;
        }
      } else {
        this.isLoggedIn = false;
        this.loggedInUser = "";
      }
    },
  },
  async created() {
    await this.checkLoginStatus();

    const urlParams = new URLSearchParams(window.location.search);
    const authCode = urlParams.get("code");

    if (authCode) {
      try {
        const kakaoApiKey = process.env.VUE_APP_KAKAO_API_KEY;
        const redirectUri = process.env.VUE_APP_KAKAO_REDIRECT_URI;

        const response = await fetch("https://kauth.kakao.com/oauth/token", {
          method: "POST",
          headers: { "Content-Type": "application/x-www-form-urlencoded" },
          body: new URLSearchParams({
            grant_type: "authorization_code",
            client_id: kakaoApiKey,
            redirect_uri: redirectUri,
            code: authCode,
          }),
        });

        const data = await response.json();
        if (data.access_token) {
          localStorage.setItem("isLoggedIn", "true");
          localStorage.setItem("accessToken", data.access_token);
          await this.checkLoginStatus();
          this.$router.push("/"); // Î°úÍ∑∏Ïù∏ ÌõÑ Ìôà ÌôîÎ©¥ÏúºÎ°ú Ïù¥Îèô
        }
      } catch (err) {
        console.error("Î°úÍ∑∏Ïù∏ Ï≤òÎ¶¨ Ï§ë Ïò§Î•ò Î∞úÏÉù:", err);
      }
    }
  },
};
</script>

<style scoped>
/* Í∏∞Î≥∏ Ïä§ÌÉÄÏùº */
body {
  margin: 0;
  font-family: Arial, sans-serif;
}

/* Ìó§Îçî Ïä§ÌÉÄÏùº */
header {
  background-color: #1e1e1e;
  color: white;
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.logo h1 {
  font-size: 24px;
  color: #00aaff;
  margin: 0;
}

/* Î©îÎâ¥ Ïä§ÌÉÄÏùº */
.menu {
  display: flex;
  gap: 20px;
}

.menu a {
  color: white;
  text-decoration: none;
  font-size: 16px;
  padding: 5px 10px;
  border-radius: 5px;
}

.menu a:hover {
  background-color: #333;
}

/* Ïù∏Ï¶ù ÏÑπÏÖò Ïä§ÌÉÄÏùº */
.auth {
  display: flex;
  align-items: center;
  gap: 10px;
}

.username {
  font-size: 16px;
  color: #00ff00;
}

.login-btn,
.logout-btn {
  background-color: #00aaff;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
}

.login-btn:hover,
.logout-btn:hover {
  background-color: #007acc;
}
</style>
